@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital@1&display=swap');
*{
  margin: 0;
  border: 0;
  padding: 0;
}
body{
  background-color: #17142c;
  color: aliceblue;
  font-size: 30px;
  font-family: 'Open Sans', sans-serif;
}
.chat{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
}
.header{
  position: fixed;
  top: 0;
  width: 100%;
  background-color: rgb(71, 58, 146);
  display: flex;
  justify-content: space-between;
  padding: 1em;
}
.header > button{
  margin-right: 1em;
  padding: 0.5em;
  font-size: 30px;
}
.header > h1{
  margin-left: 1em;
}
.messages{
  margin-top: 150px;
  margin-bottom: 250px;
  width: 90%;
}


.sendMsg{
  position: fixed;
  bottom: 0;
  width: 100%;
  /* background-color: aqua; */
  height: 200px;
  display: flex;
  justify-content: space-around;
  /* margin-bottom: 0.5em; */
}
.sendMsg > input{
  width: 70%;
  /* height: 70%; */
  height: 100px;
  padding-left: 1em;
  border-radius: 50px;
  font-size: 30px;
}
.sendMsg > button{
  width: 15%;
  height: 100px;
  padding: 0.5em;
  border-radius: 50px;
  font-size: 30px;
}
.msg{
  /* background-color: aqua; */
  display: grid;
  grid-template-columns: 100px 1fr;
  margin-top: 1em;
  padding: 1em;
}
.sent{
  background-color: rgba(240, 248, 255, 0.171);
}
.singIn{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}
.singIn > button{
  /* width: 400px;
  height: 200px; */
  font-size: 50px;
  padding: 1em; 
}
.singIn > h1{
  margin: 2em;
  font-size: 100px;
  text-align: center;
}
.singIn > h2{
  font-size: 50px;
  margin: 1em;
}

button{
  border: 3px black solid;
  background-color: rgba(240, 248, 255, 0.774);
}